:root {
    --thm-color-0: #ccc;
    --thm-color-1: #eee;
    --thm-color-P: #57bbbf;
    --thm-color-PD: #40898c;
}

#welcome-photo {
    margin: 0;
    width: 100vw;
    height: 70vh;
    background-size: cover;
    background-position: center center;
}

* {
    box-sizing: border-box;
}

main {
    position: absolute;
    overflow: auto;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

div.logo,
div.trombine {
    flex: 0 0 auto;
    content: ".";
    font-size: 0;
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: center;
    width: 6rem;
    height: 6rem;
    border: none;
    margin: 0 .5rem;
}

div.logo {
    background-image: url(./logo.svg);
}

div.trombine {
    background-image: url(./trombine.webp);
    border-radius: 50%;
}

header {
    position: relative;
    background: var(--thm-color-PD);
    margin: 0;
    padding: 0;
    color: #fffe;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 7rem;
    z-index: 2;
    box-shadow: 0 8px 16px #0007;
    text-align: center;
}
header p { white-space: nowrap; }

nav {
    background: var(--thm-color-PD);
    color: #fffe;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    height: 3rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0 8px 16px #0007;
    z-index: 1;
    padding-right: .5rem;
}
nav > div.logo {
    width: 2rem;
    height: 2rem;
}

h1,
h2 {
    font-weight: 500;
}

section {
    background: var(--thm-color-1);
    padding: 1rem;
    border-radius: 0.5rem;
    margin: 1rem;
    box-shadow: 0 1px 2px #0007;
}

li {
    position: relative;
    list-style: none;
    cursor: pointer;
}

li>div {
    position: relative;
    max-height: 3.5rem;
    overflow: hidden;
    margin-bottom: 1rem;
}

li:hover>div {
    max-height: none;
}

li>div::after {
    position: absolute;
    left: 0;
    top: 1rem;
    right: 0;
    bottom: 0;
    content: ".";
    font-size: 0;
    background-image: linear-gradient(to top, var(--thm-color-1), transparent);
}

li:hover>div::after {
    background: none;
}

h1 {
    font-size: 1.5rem;
    margin: 0
}

h2 {
    font-size: 1.25rem;
    margin: 0
}

h2::before {
    content: "â–¸";
    position: absolute;
    margin: -.25rem -1rem;
    transition: transform .3s;
}

li:hover>h2::before {
    transform: rotate(90deg);
}

body {
    background: var(--thm-color-0);
    margin: 0;
    padding: 0;
}